}

.language-menu.open .language-list {
  display: block;
}

.language-list {
  border: 1px solid;
  position: absolute;
}

.icon-arrow {
  transition: transform .3s ease-in-out;
}

.icon-arrow.rotate {
  transform: rotate(180deg);
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0 18px 0 0;
  padding: 0;
  display: flex;
}

