  background-color: rgba(0, 0, 0, .1);
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--accent-color);
  z-index: 999;
  padding: 32px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
